.msg {
  padding: 8px;
  background: #f5f5f5;
  border-radius: 2px;
  font-family: "Gilroy";
  color: #333333;
  font-size: 10px;
  line-height: 14px;
  width: 164px;
  height: auto;
  color: #333333;
}

@media (max-width: 320px) {
  .msg {
    width: 153px;
    height: auto;
  }
}
